<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section">
        <label class="slds-form-element__label enabled-label">Visualforce Pages</label>
        <div class="slds-form-element__control">
          <textarea class="slds-textarea textarea-container" v-model="vfPages.SiteWhitelistPages"></textarea>
          <div class="slds-form-element__help">Separate page names with commas.</div>
        </div>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving" @click="saveSitePagesWhitelist">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getSitePagesWhitelist, saveSitePagesWhitelist } from '../api/charket-settings'

export default {
  name: 'WhitelistedVisualforcePages',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Whitelisted Visualforce Pages'
      },
      vfPages: {
        SiteWhitelistPages: ''
      },
      isSaving: false,
      originalData: null
    }
  },
  computed: {
    hasChanged () {
      return this.originalData !== this.vfPages.SiteWhitelistPages
    }
  },
  created () {
    this.fetchSitePagesWhitelist()
  },
  methods: {
    async fetchSitePagesWhitelist () {
      try {
        this.$bus.emit('show-loading')
        const data = await getSitePagesWhitelist()
        this.originalData = data.SiteWhitelistPages
        this.vfPages.SiteWhitelistPages = data.SiteWhitelistPages
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.vfPages.SiteWhitelistPages = this.originalData
    },
    async saveSitePagesWhitelist () {
      try {
        this.isSaving = true
        await saveSitePagesWhitelist(JSON.stringify(this.vfPages))
        this.$bus.emit('success', { content: 'Whitelisted Visualforce Pages was saved.', type: 'success' })
        this.originalData = this.vfPages.SiteWhitelistPages
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    .enabled-label, .slds-form-element__help {
      color: #706E6B;
      margin-top: -2px;
    }
    .textarea-container {
      width: 446px;
      min-height:60px;
    }
  }
</style>
